import { setUsersAPI } from "../Request-API/API";

const ADD_POST = 'ADD-POST';
const TO_PAGE = 'TO_PAGE'
const POST_DELETE = 'POST_DELETE'


const SET_STATUS = 'SET_STATUS'

let initialState = {
    posts:[
        {id: 1, name: "Dimon",  likeCount: 12},
        {id: 2, name: "Stark",  likeCount: 12},
    ],
    profile: null,
    status: ""
}

const postPageReducer = (state = initialState, action) => {
    if(action.type === ADD_POST) {
        let user = {
            id: 3,
            name: action.newText,
            likeCount: 0
        }

        return {
            ...state,
            posts: [...state.posts, user],
            
        }
    } 
    else if (action.type === TO_PAGE) {
        return {
            
             ...state,
             profile: action.profile

        }

    }

    else if (action.type === SET_STATUS) {
        return {
            
             ...state,
             status: action.textStatus

        }

    } else if (action.type ===POST_DELETE ) {
        return {
            ...state,
            posts: state.posts.filter(ps => ps.id !== action.f)
        }
    }


    return state
}


export let addPostActionCreater = (newText) => {
    return {type: ADD_POST, newText }
}

export let postDelete = (f) => {
    return {type: POST_DELETE, f }
}


export let goToPage = (profile) => {return {type: TO_PAGE, profile }}


export let setStatusAC = (textStatus) => {return {type: SET_STATUS, textStatus }}



export let setProfile = (userId) => {
    return async dispatch => {
        let response = await setUsersAPI.profileAPI(userId)
            dispatch(goToPage(response.data))
    }
}


export let setStatus = (id) => {
    
    return async dispatch => {
        let response = await setUsersAPI.statusAPI(id)
            dispatch(setStatusAC(response.data))
    }
}


export let NEWsetStatus = (status) => {
    return async dispatch => {
        let response = await setUsersAPI.NEWstatusAPI(status)
            
                if(response.data.resultCode === 0) {  
                    dispatch(setStatusAC(status))
                }
    }
}



export default postPageReducer






