import React from 'react';
import {   Route, withRouter } from 'react-router-dom';
import './App.css'; 

import Nav from './components/Nav/Nav.jsx';
// import News from './components/Othep-tabs/News/News';
// import Music from './components/Othep-tabs/Music/Music';
// import Seting from './components/Othep-tabs/Seting/Seting';
import FriendsListContainer from './components/Othep-tabs/Friends_List/FriendsList-Container';
import HeaderContainer from './components/Header/Header-Container';
// import Login from './components/Othep-tabs/Login/Login';
import ContainerProfile from './components/Content/Content-Conteiner';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { initialization } from './redux/app_Reducer';
import Preloader from './components/common/Preloader/Preloader';
import { LazyHoc } from './Hoc/Lazy-Hoc.jsx';

// import DialogsConteiner from './components/Othep-tabs/Dialogs/Dialogs-Conteiner';

const DialogsConteiner = React.lazy(() => import('./components/Othep-tabs/Dialogs/Dialogs-Conteiner'))
const News = React.lazy(() => import('./components/Othep-tabs/News/News'))
const Music = React.lazy(() => import('./components/Othep-tabs/Music/Music'))
const Seting = React.lazy(() => import('./components/Othep-tabs/Seting/Seting'))
const Login = React.lazy(() => import('./components/Othep-tabs/Login/Login'))


class App extends React.Component {

  componentDidMount() {
    this.props.initialization()
  }
  render() {
    if(!this.props.initializate) return <Preloader /> 


    return ( 
      <div className="app-wrapper">
          <HeaderContainer />
          <Nav />
          <div className="app-wrapper-content">
            <Route path="/profile/:userId?" render={() => <ContainerProfile/>}/>
            <Route path="/message" render ={() => LazyHoc(DialogsConteiner)} />
            <Route path="/friends" render ={ () => <FriendsListContainer/>}/>
            <Route path="/News" render={() => LazyHoc(News)}/>
            <Route path="/Music" render={() => LazyHoc(Music)}/>
            <Route path="/Setings"  render={()=> LazyHoc(Seting)}/>
            <Route path="/Login" render={() => LazyHoc(Login)}/>
          </div>
        </div>
    )
  
  }
}

let mapStateToProps= state => ({
  initializate:state.app.initializate
})

export default compose(
  withRouter,
  connect(mapStateToProps, {initialization})
)(App);
