import React from 'react'
import moduleCss from './posts.module.css';
import Post from './post/post';




let  Posts =React.memo( props => {
    let post = [...props.posts].reverse().map(el => <Post name={el.name} age={ el.age } key={el.id} count={el.count}/>)

            return (
                <div className={moduleCss.posts}>
                    {post}
                </div>)
})

export default Posts