import React from 'react'

import './Content.css';
import Posts from './posts/posts';
import DescriptionUser from './descriptionUser/DescriptionUser';
import ContentStatus from './Content-Status';
import { reduxForm, Field } from 'redux-form';
import { Textaria } from '../common/dopComponent/Textaria';
import { required, maxLength } from '../common/validate/ComponentValidate.js';
import StatusFunc from './Status-PROTO';

let maxLength15 = maxLength(15)


let PostsField = (props) => {
    return (
        <div>
            <form onSubmit={props.handleSubmit}>
                
                <Field name={'titlePost'} component={Textaria} 
                            validate={[ required, maxLength15 ]} />
               
                            
                
            </form>
        </div>
    )
}

let PostsFieldForm =  reduxForm({form:"PostsField"})(PostsField)

function Content(props){
    

    let onSubmit = (formData) => {
        props.addPostElem(formData.titlePost)
    }

   

        return(
         <div >
               <img className="background" src={`https://oboiky.my1.ru/more/nebo-more-oblaka.jpg`}/>
               <div>
                 <DescriptionUser  profile={props.profile}/>
                 {/* <ContentStatus status ={props.status} NEWsetStatus= {props.NEWsetStatus}/> */}
                 <StatusFunc status ={props.status} NEWsetStatus= {props.NEWsetStatus}/>
                 <div className="field-text">
                     <h2>My posts</h2>
                     <PostsFieldForm  onSubmit={onSubmit} />
                    
                 </div>
                 <Posts posts={props.posts} />
               </div>
 
           </div>  
        )
}

export default Content
