import { followUserst } from "../components/Helper-Reducer/Helper-users-followed"
import {  setUsersAPI } from "../Request-API/API"

 let FOLLOW = 'users/user/FOLLOW'
 let UNFOLLOW = 'users/user/UNFOLLOW'
 let SETUSERS = 'users/SET_USERS'

 let CURRENT_COUNT = 'CURRENT_COUNT'
 let CURRENT_LIST_COUNT = 'CURRENT_LIST_COUNT'
 let SHOW = 'SHOW'
 let IS_FETCHING_PROGRESS = 'IS_FETCHING_PROGRESS'

 
let initialState = {
    users: [],
    countUsers: 7,
    currentCount: 1,
    countList: 5,
    showPicture: false,
    isFetching: []

}
    


let userPage = (state=initialState, action ) =>  {
    switch(action.type) {
        case FOLLOW: 

        return {
            ...state, 
            users: followUserst(state.users, action.userid, 'id', {followed: true})
           
        }

        case UNFOLLOW:

            return {
                ...state, 
                users:followUserst(state.users, action.userid, 'id', {followed: false})
               
            }

        case SETUSERS: {
            return {
                ...state, 
                users :[...action.users ]
            }
        }
        case CURRENT_COUNT: {
            return {
                ...state, 
                currentCount : action.list 
            }
        }

        case CURRENT_LIST_COUNT: {
            return {
                ...state, 
                countList : action.count 
            }
        }
        case SHOW: {
            return {
                ...state, 
                showPicture : action.bool 
            }
        }

        case IS_FETCHING_PROGRESS: {
            return {
                ...state, 
                isFetching : action.isFetching 
                ? [...state.isFetching, action.userid] 
                :  state.isFetching.filter( id => id !== action.userid)
            }
        }
        
      
        

        default :
            return state;
    }
};


export let followAC = userid => {
    return {type: FOLLOW, userid}
}

export let unfollowAC = (userid) => {
    return {type: UNFOLLOW, userid}
}
export let setusersAC = (users) => ({type:SETUSERS, users })
//какая сраница 
export let setCount = (list) => ({type:CURRENT_COUNT, list })

//сколько страниц
export let setCountList = (count) => ({type: CURRENT_LIST_COUNT, count })


export let showPictureDis = (bool) => ({type: SHOW, bool })


export let showProgressFollow = (isFetching, userid) => ({type: IS_FETCHING_PROGRESS, isFetching, userid})






let setShowUsersAPI = async (count , countUsers,dispatch, api, users,set ) => {
    dispatch(setCount(count))
    dispatch(showPictureDis(true))
        let response = await api(count, countUsers)
            dispatch(showPictureDis(false)) 
            dispatch(users(response.items))
            if(set) {
                
                dispatch(set(response.totalCount))
            }
} 

export let setusers = (list, countUsers) => {
    return (dispatch) => {
        setShowUsersAPI(list, countUsers, dispatch, setUsersAPI.usersAPI, setusersAC, setCountList)
        
    }
}
        
export let setusersPage = (count, countUsers) => {
    return (dispatch) => {
        setShowUsersAPI(count, countUsers, dispatch, setUsersAPI.usersPagesAPI, setusersAC)
    }
}
















const showSubscribe = async (id, followed, dispatch, api) => {
        dispatch(showProgressFollow(true, id))
        let response = await api(id)
                if(!response.data.resultCode ) {
                    dispatch(followed(id))
                }
                dispatch(showProgressFollow(false, id))
    
}

export let follow = (id) => {
    return dispatch => {
        showSubscribe(id, followAC, dispatch, setUsersAPI.followedAPI)
    }
}


export let unfollow = (id) => {
    return (dispatch) => {
        showSubscribe(id, unfollowAC, dispatch, setUsersAPI.unfollowedAPI)
        
    }
}






export default userPage