
const NEW_MESSAGE_USER = 'NEW-MESSAGE-USER ';

let initialState = {
    dialogs: [
        {id: 1, name:"Egor"},
        {id: 2, name:"Alex"},
        {id: 3, name:"Grigorey"},
        {id: 4, name:"Natasha"},
        {id: 5, name:"Masha"},
        {id: 6, name:"Tony"}
      ],
          
    messages:[
        {id:1, message: 'Hi'},
        {id:2, message: 'What you name'},
        {id:3, message: 'Me Alex'},
      ],

}

const dialogsPageReducer = (state = initialState, action) => {

    if (action.type === NEW_MESSAGE_USER ) {
        let newMessageUser = {
            id:6, 
            message: action.newMessageText
        }
        
        return {
            ...state,
            messages: [...state.messages, newMessageUser],
        }
    
        } 
       return state
    
}

export let  addMessageActionCreater = (newMessageText) => {
    return {type: NEW_MESSAGE_USER, newMessageText}
}



export default dialogsPageReducer