import React from 'react';
import './Header.css';
import { NavLink } from 'react-router-dom';


let Header = (props) => {
        return(

          <header className="header">
            <img src={`
            https://staticpasa.cdnstatics.com/static/upl/img//01FA96/sliderImgPrincipal_443_sliderImgPrincipal_443_1-slider-png-dodge-910x584-1_1534934783433_1559555152753.png`}/>  
            <h1>Project Alex</h1>
            <div className={"positionLog"}>
              {
                props.auth ? 
                <div><p>{props.login}</p> <button onClick={props.logOutAPI} className={"button logOut"}>Log out</button></div>
                : <NavLink to={`/login/` + props.userId} >  Login</NavLink> 

                
              }
            
            </div>
          </header>
        )
}

export default Header
