
import { stopSubmit } from "redux-form";
import {   authAPI } from "../Request-API/API";

let SET_USER_AUTH = 'SET_USER_AUTH'




let initialState = {
    email: null,
    login: null,
    userId: null,
   isfetch: false
}
   

let AuthReducer = (state=initialState, action ) =>  {
   switch(action.type) {
       case SET_USER_AUTH: 
       return {
           ...state, 
           ...action.data,
           
       }


       default :
           return state;
   }
}


export let setUserAuth =(userId, email, login, isfetch=true) => ({type: SET_USER_AUTH , data: {login, email, userId, isfetch}})


export let setlogin = (isfetch) => {
    return async dispatch => {
        let response = await authAPI.me()
                    if(response.resultCode === 0) {
                        let {id, email , login} = response.data
                        dispatch(setUserAuth(id, email, login))
                    } 

    }
}


export let loginAPI = (email, password,   rememberMe, isfetch = true) => {
    return  async dispatch => {
        let response =await authAPI.login(email, password,   rememberMe)
                if(response.data.resultCode === 0) {
                    dispatch(setlogin())
                }else {
                    dispatch(stopSubmit('login', {_error:response.data.messages.length > 0 ? "not Validate" : "Some error"}))
                }
    }
}

export let logOutAPI = () => {
    return async dispatch => {
        let response = await authAPI.logOut()
                if(response.data.resultCode === 0) {
                    dispatch(setUserAuth(null, null, null, false))
                }
    }
}




export default AuthReducer