import React from 'react'
import s from './Friends.module.css'
import userh from '../../assets/image/images.png'
import { NavLink } from 'react-router-dom'

let User = ({users, isFetching, follow, unfollow}) => {
   
    return (
        <div className={s.wrapper}>

           
            {
                users.map ( u => (
                    <div className={s.wrapperSubscriber} key={u.id}>
                         <div className={s.subscriber}>
                             <div>
                                <NavLink to={'/profile/' + u.id}>
                                    <img src={u.photos.small !== null ? u.photos.small : userh}alt=""/>
                                </NavLink>
                             </div>
                             { u.followed === true
                              ?<button disabled={isFetching.some( id => id ===u.id)} 
                                    onClick={ () => {unfollow(u.id)}
                                    }>unfollowed</button> :
                                    
                                    
                                <button disabled={isFetching.some( id => id ===u.id)} 
                                    onClick={ () => {follow(u.id)}
                                    }>followed</button>}
                        </div>

                        <div className={s.descriptionSubscriber}>
                            <div>
                                <h2>{u.name}</h2>
                                <p>{u.status}</p>
                            </div>

                            <div>
                                <p>{'u.location.country'}</p>
                                <p>{'u.location.city'}</p>
                            </div>
                        </div>

                    </div>
                ))
            }


        </div>
    )
}

export default User