import React from 'react'

import './Content.css';



class ContentStatus extends React.Component{
    
    state =( {
       editMode:false,
       status: this.props.status
    })

    activeChange = () => {
        this.setState({
            editMode: true
        })
    }

    deActiveChange = () => {
        this.setState({
            editMode: false
        })
        
        this.props.NEWsetStatus(this.state.status)
    }

    textIput = (e) => {
        this.setState({
            status: e.currentTarget.value
        })
        
    }

    componentDidUpdate(prevProps, prevState) {
        if(prevProps.status !== this.props.status) {
            this.setState({
                status:this.props.status
            })
        }
    }
    
    render () {

        return(
         <div className="Status">
                {!this.state.editMode ?<div><h2 onDoubleClick={ this.activeChange}>{this.props.status || "hi order"}</h2></div> 
                : <div><input type="text" onBlurCapture={this.deActiveChange} autoFocus={true} 
                value={this.state.status} onChange={this.textIput}/></div>}
                
               
                 
           </div>  

        )

    }
}

export default ContentStatus
