import React from 'react'

import './Content.css';
import { addPostActionCreater, goToPage, setProfile, setStatus, NEWsetStatus } from '../../redux/postPage_Reducer';
import { connect } from 'react-redux';

import { withRouter } from 'react-router-dom';
import Content from './Content.jsx';
import  { authRendering } from '../../Hoc/Hoc';
import { compose } from 'redux';
import Preloader from '../common/Preloader/Preloader';
import { getisfetch, getuserId } from '../../redux/auth-selector';
import { getposts, getprofile, getstatus } from '../../redux/postPage-selector';


class ContentConteiner extends React.Component{

    componentDidMount() {
        let userId = this.props.match.params.userId
        if(!userId ) { 
            userId = this.props.meId
            if(!userId) {
                this.props.history.push("/Login")
            }
        }
        this.props.setStatus(userId)
        this.props.setProfile(userId)
    }
    
    render () {
        return !this.props.profile ? <Preloader/> : <Content 
                                                        {...this.props} 
                                                        profile = {this.props.profile} 
                                                        status={this.props.status} 
                                                        NEWsetStatus={this.props.NEWsetStatus}
                                                        addPostElem={this.props.addPostActionCreater}
                                                        />
        }
}





let postState = (state) => ({ posts: getposts(state),
        profile: getprofile(state),
        auth: getisfetch(state),
        meId: getuserId(state),
        status: getstatus(state),})




export default compose(
    authRendering,
    withRouter,
    connect (postState, {
        addPostActionCreater,
        goToPage,
        setProfile,
        setStatus,
        NEWsetStatus
    })
    )(ContentConteiner)
   
    





