import React from 'react';
import './Header.css';
import Header from './Header';
import { connect } from 'react-redux';
import { logOutAPI } from '../../redux/auth_Reducer';
import { withRouter } from 'react-router-dom';
import { getemail, getisfetch, getlogin, getuserId } from '../../redux/auth-selector';

class HeaderContainer extends React.Component {

   
    
    render() {
        return <Header {...this.props} /> 
    }
}


let mapStateToProps = (state) => {
    return {
        userId:getuserId(state),
        login:getlogin(state),
        email:getemail(state),
        auth:getisfetch(state),
    }
}

let WhthHeaderContainer = withRouter(HeaderContainer)

export default connect(mapStateToProps, { logOutAPI}) (WhthHeaderContainer)
