import React, { useEffect } from 'react'
import { useState } from 'react';




let StatusFunc = (props) => {
    let [iditMofe, updateEditMofe] = useState(false)
    let [status, updateStatus ] = useState(props.status)
    

    let updateStatusServer = () => {
        props.NEWsetStatus(status)
        updateEditMofe(false)
    }
    
    let upOnCH = (e) => {
        updateStatus(e.currentTarget.value)
    }


    useEffect( () => {
        console.log('render');
        updateStatus(props.status)
    }, [props.status])


        return(
         <div className="Status">
                {
                !iditMofe ?
                    <div>
                        <span onDoubleClick={() => { updateEditMofe(true)}}>{props.status || "hi order"}</span>
                    </div>  
                
                
                
                : <div>
                    <input type="text" onBlurCapture={updateStatusServer } autoFocus={true} 
                                                        value={status} onChange={upOnCH}/>
                </div>}
                
               
                 
           </div>  

        )

    
}

export default StatusFunc
