import React from 'react'
import s from './Friends.module.css'
import Pages from '../../common/Paginator/Pages'
import User from './User'

let Users = (props) => {
   
    return (
        <div className={s.wrapper}>

            <h1 className={s.text}>Possible Friends</h1>

            <Pages countUsers= {props.countUsers}
                    countList= {props.countList}
                    setusersPage= {props.setusersPage}
                    currentCount={props.currentCount}
                    users= {props.users}/>

           
            
                <User 
                users= {props.users}
                isFetching= {props.isFetching}
                follow= {props.follow}
                unfollow={props.unfollow}/>
            


        </div>
    )
}

export default Users