import * as Axios from "axios"

const instanc = Axios.create({
    withCredentials:true,
    baseURL: `https://social-network.samuraijs.com/api/1.0/`,
    headers: {
        "API-KEY": '62553766-0fac-4e12-b410-b4d6b8eee5e5'
    }
})

export let setUsersAPI = {
    profileAPI (userId) {
        return instanc.get(`profile/` + userId)
            
    },
    statusAPI (s) {
        return instanc.get(`profile/status/`+ s)
            
    },

    NEWstatusAPI (status) {
        return instanc.put(`profile/status/` , {status})
            
    },
    
    
    
    
    usersAPI (list, countUsers) {
        return instanc.get(`users?page=${list}&count=${countUsers}`)
            .then(response => {
                return response.data
            })
    },
    
    usersPagesAPI  (count, countUsers)  {
        return instanc.get(`users?page=${count}&count=${countUsers}`)
            .then(response => {
                return response.data
            })
    },
    
    
    unfollowedAPI  (id)  {
        return instanc.delete(`follow/${id}`)
    },
    
    followedAPI (id)  {
        return instanc.post(`follow/${id}`)
    }
    
}


export let authAPI = {
    me () {
        return instanc.get('auth/me')
        .then(response => {
            return response.data
        })
            
    },
    login(email, password, rememberMe = false) {
        return instanc.post('auth/login', {email, password, rememberMe })
    },
    logOut() {
        return instanc.delete('auth/login/')
    }
}



