import React from 'react'


import { connect } from 'react-redux';
import { Redirect} from 'react-router-dom';
import Preloader from '../components/common/Preloader/Preloader';


let mapStateToProps = (state) => {
    return {
        auth: state.auth.isfetch,
    }
}


export let authRendering = (Component) => {
    class  RedirectContainer  extends React.Component{ 
        render() {
            if(!this.props.auth) return <Redirect to='/Login'/>
           
            return <Component {...this.props} />
        }
        
    }
    let authRenderingComponent = connect(mapStateToProps)(RedirectContainer) 
    return authRenderingComponent 
}




