import { createStore, combineReducers, applyMiddleware, compose } from "redux";
import dialogsPageReducer from "./dialogsPage_Reducer";
import postPageReducer from "./postPage_Reducer.js";
import  sidebarReaducer  from  "./sidebar_Readucer";
import userPage from "./userPage-Reducer";
import AuthReducer from "./auth_Reducer";
import thunkMiddleWare from "redux-thunk";
import { reducer as formReducer } from "redux-form";
import AppReducer from "./app_Reducer";



let reducers = combineReducers({
    dialogsPage: dialogsPageReducer,
    postPage: postPageReducer,
    sidebar: sidebarReaducer,
    usersPage: userPage,
    auth: AuthReducer,
    form: formReducer,
    app: AppReducer    
})


const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers,  composeEnhancers(applyMiddleware(thunkMiddleWare)))


// let store = createStore(reducers, applyMiddleware(thunkMiddleWare))

window.store = store


export default store