import React, { useState } from 'react'
import s from '../../Othep-tabs/Friends_List/Friends.module.css'


let Pages = (props) => {
   
      let  portionSize = 10
    let onClickUsers = (count) => {
            
        props.setusersPage(count, props.countUsers)
    }

    let list = Math.ceil(props.countList /props.countUsers  )


    let page= []
    for(let i =1; i <= list; i++) {
        page.push(i)
    }

    let portionCount = Math.ceil(list / portionSize)
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionPagesNumber = (portionNumber -1 ) * portionSize +1 
    let rightPortionPagesNumber = portionNumber  * portionSize




    return (
        <div className={s.wrapper}>
           <div>
           {
                portionNumber > 1 && <button onClick={() => {setPortionNumber(portionNumber - 1)}}>PREV</button>
            }
           </div>


            {
                page.filter(p => {
                        return p >= leftPortionPagesNumber && p<= rightPortionPagesNumber
                }).map( p => {
                    console.log(p);
                        return (<span onClick={(e) => {onClickUsers(p)} } className={p === props.currentCount ? s.boldSTR : s.thin}>{p}</span>)
                    
                })
            }

           <div>
           {
                portionCount > portionNumber 
                && <button onClick={() => {setPortionNumber(portionNumber + 1)}}>NEXT</button>
            }
           </div>


        </div>
    )
}

export default Pages