
import { setlogin } from "./auth_Reducer";

let INITIALISATE_SUCCES = 'INITIALISATE_SUCCES'




let initialState = {
    initialzate:false
}
   

let AppReducer = (state=initialState, action ) =>  {
   switch(action.type) {
       case INITIALISATE_SUCCES: 
       return {
           ...state, 
           initializate: true,
           
       }


       default :
           return state;
   }
}


export let initializateSuccess =() => ({type: INITIALISATE_SUCCES })


export let initialization = () => (dispatch) => {
    let promise = dispatch(setlogin())
         Promise.all([promise])
        .then ( () => {
            dispatch(initializateSuccess())
        })
}

export default AppReducer



