import React from 'react'

import FieldText from './DescriptionUser.module.css';



let DescriptionUser = (props) => {
    return(
        <div className={FieldText["content-page_website"]}>
            <img src={ props.profile.photos.large }/>
            <div className={FieldText.description}>
                <h2>{props.profile.fullName}</h2>

                <p>{props.profile.aboutMe}</p>
                <p>{props.profile.lookingForAJobDescription}</p>
                <p>{props.profile.contacts.facebook}</p>
                <p>{props.profile.contacts.instagram}</p>
                <p>{props.profile.contacts.twitter}</p>
                
            </div>
            
        </div>
              
    )

}

export default DescriptionUser