import React from 'react'
import  { connect } from "react-redux";
import { follow, unfollow, showProgressFollow, setusers, setusersPage } from '../../../redux/userPage-Reducer';
import Users from './Users'
import { compose } from 'redux';
import Preloader from '../../common/Preloader/Preloader';
import { getcountList, getcountUsers, getcurrentCount, getisFetching, getshowPicture, getUsers, getwithCredentials } from '../../../redux/users-selector';



class PossibleFriends  extends React.Component {

    componentDidMount () {
       
        
        this.props.setusers(1, this.props.countUsers)
    }
    
    render () {
        
        
        

    return (
        <div>
            {this.props.showPicture?  <Preloader /> : 
                <Users 
                    currentCount={this.props.currentCount}
                    users={this.props.users}
                    unfollow={this.props.unfollow}
                    follow={this.props.follow}

                    countUsers={this.props.countUsers}
                    countList={this.props.countList}
                    setusersPage={this.props.setusersPage}

                    showPicture={this.props.showPicture}
                    showProgressFollow={this.props.showProgressFollow}
                    isFetching={this.props.isFetching}

                />}
        </div>)

    }
    
    
}   



let mapStateToProps = (state) => {
    return {
        users:getUsers(state),
        countUsers: getcountUsers(state),
        currentCount: getcurrentCount(state),
        countList: getcountList(state),
        showPicture:getshowPicture(state),
        isFetching:getisFetching(state),
        isFetch: getwithCredentials(state),
    }
}







export default compose(
    
    connect(mapStateToProps, {
        follow,
        unfollow,
        showProgressFollow,
        setusers,
        setusersPage,
        })
) (PossibleFriends)














