import React from 'react'
import { Field } from 'redux-form'
import Preloader from '../Preloader/Preloader'


export let Textaria = ({input, meta, ...props}) => {
    return (
        <div className={"targetError"}>
            {
                ( meta.error && meta.touched) ? 
                        <input {...input}  type={'text'} className={"text", 'error'} placeholder={meta.error} />
                    : <input {...input}  placeholder='new post' type={'text'} className={"text"} />
            }
            <button className="button" >Send</button>
        </div>
    )
}




export let AuthPage = ({input, meta, ...props}) => {
    
    return <div className={"targetError"}>
            {
                ( meta.error && meta.touched)  
                    ?<div>
                        <input {...input}  className={"text", 'error'} placeholder={meta.error} />
                        { !meta.error && meta.active && meta.touched ?
                            null:
                            <p style={{color:'red', margin:'0'}}>{meta.error}</p>
                        }
                    </div> 
                    : !meta.error && meta.touched ? 
                    <input {...input}  className={"text"} placeholder={props.text} autoFocus={true}/>
                    :<input {...input}  className={"text"} placeholder={props.text} />
            }
           
        </div>
    
}


export let PassWord = ({input, meta, ...props}) => {
    return  <AuthPage input={input} meta={meta} {...props} text={'password'} />
}


export let LogIN = ({input, meta, ...props}) => {
    
    return <AuthPage input={input} meta={meta} {...props}  text={'login'}/>  
}


export let createField = (name, component, validate, type, text) => { 
    return (
        <div>
            <Field name={name} component={component}  validate={validate} type={type}/> 
            {text}
        </div>
    )
}