import { createSelector } from "reselect"





export const getUsersSelector = (state) => {
    return state.usersPage.users
}



export const getUsers = createSelector (getUsersSelector, 
    (users) => {
        return users.filter(u => true)
    }
)


export const getcountUsers = (state) => {
    return state.usersPage.countUsers
}
export const getcurrentCount = (state) => {
    return state.usersPage.currentCount
}
export const getcountList = (state) => {
    return state.usersPage.countList
}
export const getshowPicture = (state) => {
    return state.usersPage.showPicture
}
export const getisFetching = (state) => {
    return state.usersPage.isFetching
}
export const getwithCredentials = (state) => {
    return state.auth.withCredentials
}